"use client";

import { useEffect, useMemo, useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { predictMap, fetchTeams, MapPredictResponse } from "@/lib/api";

const MAPS = ["Ascent","Bind","Breeze","Fracture","Haven","Icebox","Lotus","Pearl","Split","Sunset"];

export default function PredictForm() {
  const [teams, setTeams] = useState<string[]>([]);
  const [teamA, setTeamA] = useState("");
  const [teamB, setTeamB] = useState("");
  const [mapName, setMapName] = useState<string | undefined>("Ascent");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [result, setResult] = useState<MapPredictResponse | null>(null);

  useEffect(() => {
    fetchTeams().then(setTeams).catch(() => setTeams([]));
  }, []);

  async function onPredict() {
    setLoading(true);
    setError(null);
    setResult(null);
    try {
      const data = await predictMap({ teamA, teamB, map_name: mapName });
      setResult(data);
    } catch (e: any) {
      setError(e?.message || "Prediction failed");
    } finally {
      setLoading(false);
    }
  }

  const probA = result?.prob_teamA ?? null;
  const probB = result?.prob_teamB ?? null;
  const winner = useMemo(() => {
    if (probA == null || probB == null) return null;
    return probA >= probB ? teamA : teamB;
  }, [probA, probB, teamA, teamB]);

  return (
    <div className="space-y-6">
      <div className="grid gap-4 sm:grid-cols-2">
        <div>
          <Label>Team A</Label>
          <Input list="teams-list" value={teamA} onChange={(e) => setTeamA(e.target.value)} placeholder="Type or pick a team" />
        </div>
        <div>
          <Label>Team B</Label>
          <Input list="teams-list" value={teamB} onChange={(e) => setTeamB(e.target.value)} placeholder="Type or pick a team" />
        </div>
        <datalist id="teams-list">
          {teams.map((t) => (
            <option key={t} value={t} />
          ))}
        </datalist>
        <div>
          <Label>Map</Label>
          <Select value={mapName} onValueChange={(v) => setMapName(v)}>
            <SelectTrigger>
              <SelectValue placeholder="Select a map" />
            </SelectTrigger>
            <SelectContent>
              {MAPS.map((m) => (
                <SelectItem key={m} value={m}>{m}</SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <Button disabled={!teamA || !teamB || loading} onClick={onPredict}>
        {loading ? "Predicting..." : "Predict Map"}
      </Button>

      {error && <p className="text-red-600 text-sm">{error}</p>}

      {result && (
        <div className="mt-4 rounded-lg border p-4">
          <h3 className="text-lg font-semibold">{winner ? `${winner} favored` : "Result"}</h3>
          <p className="text-sm text-muted-foreground">
            {teamA}: {(probA! * 100).toFixed(1)}% â€¢ {teamB}: {(probB! * 100).toFixed(1)}%
          </p>
          {result.explanation && (
            <p className="mt-2 text-sm">{result.explanation}</p>
          )}
        </div>
      )}
    </div>
  );
}
